<template>
	<view class="content">
		<view class="science-area">
			<scroll-view scroll-x="true">
				<view class="science-view">
					<view class="science"><text>{{scienceText}}</text></view>
					<view class="science"><text>{{scienceText}}</text></view>
					<view class="science"><text>{{scienceText}}</text></view>
					<view class="science"><text>{{scienceText}}</text></view>
				</view>
			</scroll-view>
		</view>
		
		<view class="selected-area">
			<scroll-view scroll-x="true">
				<view class="selected-view">
					<view class="selected"><text>{{selectedText}}</text></view>
					<view class="selected"><text>{{selectedText}}</text></view>
					<view class="selected"><text>{{selectedText}}</text></view>
					<view class="selected"><text>{{selectedText}}</text></view>
				</view>
			</scroll-view>
		</view>
		
		<view class="cut-line"></view>
		
		<view class="content-area">
			<view class="content-view" v-for="(item, index) in contents" :key="index">
				<view>
					<image :src="item.avatar" mode="aspectFill"></image>
					<view>
						<text>{{item.nickname}}</text>
						</br>
						<text style="font-size: 22rpx;color: #999999;">{{item.time}}</text>
					</view>
					<text style="font-size: 22rpx;color: #55aaff;">{{item.top ? "↑置顶" : ""}}</text>
				</view>
				<view>{{item.title}}</view>
				<view>
					<image v-for="(i, index) in item.pic" :key="index" mode="aspectFill" :src="i" @click="previewImage(item.pic)"></image>
					
				</view>
				
			</view>
			
		</view>
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				scienceText: "宠物百科",
				selectedText: "#我要领养",
				contents: [
					{
						"avatar": "https://img1.baidu.com/it/u=1256925850,3490340175&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=500",
						"nickname": "吃货小铭",
						"title": "晒一晒我家萌宠-雪糕",
						"time": "2小时前",
						"top": true,
						"pic": [
							"https://img2.baidu.com/it/u=659218148,4156080475&fm=26&fmt=auto",
							"https://img1.baidu.com/it/u=2778768044,384696851&fm=253&fmt=auto&app=120&f=JPEG?w=844&h=500"
						]
					},
					{
						"avatar": "https://img0.baidu.com/it/u=1801510667,176844939&fm=26&fmt=auto",
						"nickname": "不灵不灵",
						"title": "想领养一只猫咪，自己家养不限品种，主要是想等它长大帮我捉老鼠。</br>希望无偿或有偿领养。",
						"time": "8小时前",
						"top": false,
						"pic": ""
					}
				],
				imgs: []
			}
		},
		onLoad() {

		},
		methods: {
			// 预览图片
			previewImage(item) {
				uni.previewImage({
					current: item[0],
					urls: item,
					// loop: true   // 是否可循环预览
				});
			}
		}
	}
</script>

<style lang="scss">
	.content {
		display: flex;
		flex-direction: column;
		/* align-items: center; */
		justify-content: center;
		padding: 15rpx;
	}
	
	scroll-view {
		white-space: nowrap;
	}
	
	.science-area .science:nth-child(1) {
		background-color: #FCF5C9;
	}
	.science-area .science:nth-child(2) {
		background-color: #EEF9FF;
	}
	.science-area .science:nth-child(3) {
		background-color: #F2FCE8;
	}
	.science-area .science:nth-child(4) {
		background-color: #FFEEEF;
	}
	
	.science-area .science-view .science {
		float: left;
		padding: 30rpx;
		margin: 10rpx;
		border-radius: 10rpx;
		font-size: $uni-font-size-base;
	}
	
	.science-area .science-view {
		display: inline-block;
	}
	
	.selected-area .selected-view {
		display: inline-block;
	}
	
	.selected-area .selected-view .selected {
		background-color: #F3FAFF;
		color: #007AFF;
		float: left;
		margin: 15rpx;
		border-radius: 50rpx;
		font-size: $uni-font-size-sm;
	}
	
	.cut-line {
		width: 100%;
		height: 6rpx;
		background-color: #efefef;
	}
	
	.content-area {
		margin-top: 25rpx;
		
		.content-view {
			clear: both;
			margin-bottom: 400rpx;
		}
		
		.content-view view:first-child {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
		
		.content-view view:nth-child(2) {
			margin-top: 20rpx;
			margin-bottom: 20rpx;
			font-size: 30rpx;
		}
		
		.content-view view:nth-child(3) {
			height: 100%;
			
			image {
				width: 30%;
				height: 320rpx;
				float: left;
				margin: 10rpx;
			}
		}
		
		.content-view view:first-child image {
			width: 100rpx;
			height: 100rpx;
		}
		
		.content-view view:first-child view {
			margin-left: -370rpx;
		}
	}

</style>
